'use client'

import { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslations } from 'next-intl'
import { AchievementCard } from './AchievementCard'
import { ACHIEVEMENTS } from '@/lib/rewards/achievements'
import type { Achievement } from '@/types'

interface AchievementsPanelProps {
  unlockedAchievementIds: string[]
}

type Category = 'all' | 'deals' | 'training' | 'engagement' | 'milestones'

const CATEGORIES: Category[] = ['all', 'deals', 'training', 'engagement', 'milestones']

export function AchievementsPanel({ unlockedAchievementIds }: AchievementsPanelProps) {
  const t = useTranslations('rewards')
  const [selectedCategory, setSelectedCategory] = useState<Category>('all')

  // Filter achievements by category
  const filteredAchievements =
    selectedCategory === 'all'
      ? ACHIEVEMENTS
      : ACHIEVEMENTS.filter((achievement) => achievement.category === selectedCategory)

  // Calculate unlocked count
  const unlockedCount = filteredAchievements.filter((achievement) =>
    unlockedAchievementIds.includes(achievement.id)
  ).length
  const totalCount = filteredAchievements.length

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-white">{t('achievements.title')}</h2>
          <p className="mt-1 text-sm text-gray-400">
            {t('achievements.unlocked', { count: unlockedCount, total: totalCount })}
          </p>
        </div>

        {/* Category Filter */}
        <div className="flex gap-2">
          {CATEGORIES.map((category) => (
            <button
              key={category}
              onClick={() => setSelectedCategory(category)}
              className={`
                rounded-lg px-4 py-2 text-sm font-medium transition-all
                ${
                  selectedCategory === category
                    ? 'bg-blue-600 text-white shadow-sm'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200 dark:bg-gray-800 dark:text-gray-300 dark:hover:bg-gray-700'
                }
              `}
            >
              {t(`achievements.categories.${category}`)}
            </button>
          ))}
        </div>
      </div>

      {/* Achievements Grid */}
      <motion.div
        layout
        className="grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-3"
      >
        <AnimatePresence mode="popLayout">
          {filteredAchievements.map((achievement) => (
            <motion.div
              key={achievement.id}
              layout
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.8 }}
              transition={{ duration: 0.2 }}
            >
              <AchievementCard
                achievement={achievement}
                unlocked={unlockedAchievementIds.includes(achievement.id)}
              />
            </motion.div>
          ))}
        </AnimatePresence>
      </motion.div>
    </div>
  )
}
