/**
 * Check if an achievement condition is met
 */
export function checkAchievementUnlocked(
  achievement: Achievement,
  stats: PartnerStats
): boolean {
  return achievement.condition(stats);
}

/**
 * Get all achievements that should be unlocked for given stats
 */
export function getUnlockedAchievements(
  stats: PartnerStats,
  currentlyUnlocked: string[]
): Achievement[] {
  return ACHIEVEMENTS.filter(
    (achievement) =>
      !currentlyUnlocked.includes(achievement.id) &&
      checkAchievementUnlocked(achievement, stats)
  );
}
