'use client';

import { motion } from 'framer-motion';
import { Crown, TrendingUp } from 'lucide-react';
import { useTranslations } from 'next-intl';
import type { LeaderboardEntry } from '@/types';

interface LeaderboardCardProps {
  entries: LeaderboardEntry[];
  currentPartnerId?: string;
}

export function LeaderboardCard({ entries, currentPartnerId }: LeaderboardCardProps) {
  const t = useTranslations('rewards');

  // Position badges for top 3
  const positionBadges: Record<number, string> = {
    1: 'bg-gradient-to-br from-yellow-400 to-yellow-600',
    2: 'bg-gradient-to-br from-gray-300 to-gray-500',
    3: 'bg-gradient-to-br from-orange-400 to-orange-600',
  };

  return (
    <div className="bg-gray-800 rounded-lg shadow-sm p-6 space-y-4">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h2 className="text-xl font-semibold text-white">
          {t('leaderboard.title')}
        </h2>
        <Crown className="w-6 h-6 text-yellow-500" />
      </div>

      {/* Entries */}
      <div className="space-y-2">
        {entries.length === 0 ? (
          <div className="text-center py-8 text-gray-500 dark:text-gray-400">
            {t('leaderboard.noData')}
          </div>
        ) : (
          entries.map((entry, index) => {
            const isCurrentPartner = entry.partnerId === currentPartnerId;
            const isTop3 = entry.rank <= 3;

            return (
              <motion.div
                key={entry.partnerId}
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{
                  duration: 0.3,
                  delay: index * 0.1,
                }}
                className={`
                  flex items-center gap-4 p-4 rounded-lg transition-all duration-200
                  ${isCurrentPartner
                    ? 'bg-blue-50 dark:bg-blue-900/20 border-2 border-blue-500'
                    : 'bg-gray-50 dark:bg-gray-700/50 border border-gray-700'
                  }
                  hover:shadow-md
                `}
              >
                {/* Position Badge */}
                <div
                  className={`
                    flex items-center justify-center w-10 h-10 rounded-full font-bold text-white flex-shrink-0
                    ${isTop3 ? positionBadges[entry.rank] : 'bg-gray-400 dark:bg-gray-600'}
                  `}
                >
                  {entry.rank}
                </div>

                {/* Partner Name */}
                <div className="flex-1 min-w-0">
                  <div className="font-semibold text-white truncate">
                    {entry.name}
                    {isCurrentPartner && (
                      <span className="ml-2 text-sm text-blue-600 dark:text-blue-400">
                        {t('leaderboard.you')}
                      </span>
                    )}
                  </div>
                </div>

                {/* Points */}
                <div className="flex items-center gap-2 flex-shrink-0">
                  {isTop3 && (
                    <TrendingUp className="w-4 h-4 text-green-500" />
                  )}
                  <span className="font-bold text-white">
                    {entry.points.toLocaleString()}
                  </span>
                  <span className="text-sm text-gray-500 dark:text-gray-400">
                    {t('leaderboard.points')}
                  </span>
                </div>
              </motion.div>
            );
          })
        )}
      </div>
    </div>
  );
}
