'use client';

import { motion } from 'framer-motion';
import {
  Briefcase,
  TrendingUp,
  CheckCircle,
  DollarSign,
  Award,
  Star,
  Users,
  FileText,
  Clock,
  Trophy,
} from 'lucide-react';
import { cn } from '@/lib/utils';

const iconMap = {
  briefcase: Briefcase,
  'trending-up': TrendingUp,
  'check-circle': CheckCircle,
  'dollar-sign': DollarSign,
  award: Award,
  star: Star,
  users: Users,
  'file-text': FileText,
  clock: Clock,
  trophy: Trophy,
} as const;

type IconName = keyof typeof iconMap;

interface StatsCardProps {
  title: string;
  value: string | number;
  iconName: IconName;
  trend?: {
    value: number;
    isPositive: boolean;
  };
  color?: 'primary' | 'green' | 'purple' | 'orange';
}

export function StatsCard({ title, value, iconName, trend, color = 'primary' }: StatsCardProps) {
  const colors = {
    primary: {
      bg: 'bg-blue-500/10',
      border: 'border-blue-500/20',
      icon: 'text-blue-400',
    },
    green: {
      bg: 'bg-green-500/10',
      border: 'border-green-500/20',
      icon: 'text-green-400',
    },
    purple: {
      bg: 'bg-purple-500/10',
      border: 'border-purple-500/20',
      icon: 'text-purple-400',
    },
    orange: {
      bg: 'bg-orange-500/10',
      border: 'border-orange-500/20',
      icon: 'text-orange-400',
    },
  };

  const Icon = iconMap[iconName];

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="rounded-2xl border border-gray-800 bg-gray-900 p-4 sm:p-6"
    >
      <div className="flex items-start justify-between">
        <div className="min-w-0 flex-1">
          <p className="text-xs sm:text-sm font-medium text-gray-400 truncate">{title}</p>
          <p className="mt-1 sm:mt-2 text-2xl sm:text-3xl font-bold text-white">{value}</p>
          {trend && (
            <p
              className={cn(
                'mt-1 sm:mt-2 text-xs sm:text-sm font-medium',
                trend.isPositive ? 'text-green-400' : 'text-red-400'
              )}
            >
              {trend.isPositive ? '+' : ''}{trend.value}%
            </p>
          )}
        </div>
        <div className={cn('rounded-xl p-2 sm:p-3 border shrink-0', colors[color].bg, colors[color].border)}>
          <Icon className={cn('h-5 w-5 sm:h-6 sm:w-6', colors[color].icon)} />
        </div>
      </div>
    </motion.div>
  );
}
