'use client';

import { motion, useSpring } from 'framer-motion';
import { Trophy, TrendingUp } from 'lucide-react';
import { useEffect } from 'react';
import { useTranslations } from 'next-intl';
import type { RewardStatus } from '@/types';

interface RewardsCardProps {
  status: RewardStatus;
}

export function RewardsCard({ status }: RewardsCardProps) {
  const t = useTranslations('rewards');

  // Spring animation for points
  const animatedPoints = useSpring(0, {
    stiffness: 100,
    damping: 30,
  });

  useEffect(() => {
    animatedPoints.set(status.totalPoints);
  }, [status.totalPoints, animatedPoints]);

  // Calculate progress to next milestone
  const progressPercent = status.nextMilestone
    ? ((status.totalPoints - (status.nextMilestone - status.pointsToNextMilestone)) /
        status.pointsToNextMilestone) *
      100
    : 100;

  // Tier colors
  const tierColors: Record<string, string> = {
    bronze: 'bg-orange-500 text-white',
    silver: 'bg-gray-400 text-white',
    gold: 'bg-yellow-500 text-white',
    platinum: 'bg-purple-500 text-white',
  };

  return (
    <div className="bg-gray-800 rounded-lg shadow-sm p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h2 className="text-xl font-semibold text-white">
          {t('title')}
        </h2>
        <Trophy className="w-6 h-6 text-yellow-500" />
      </div>

      {/* Total Points */}
      <div className="space-y-2">
        <div className="text-sm text-gray-400">{t('totalPoints')}</div>
        <motion.div className="text-4xl font-bold text-white">
          {Math.floor(animatedPoints.get()).toLocaleString()}
        </motion.div>
      </div>

      {/* Progress to Next Milestone */}
      {status.nextMilestone && (
        <div className="space-y-2">
          <div className="flex justify-between items-center">
            <span className="text-sm text-gray-400">
              {t('nextMilestone')}
            </span>
            <span className="text-sm font-medium text-white">
              {status.nextMilestone.toLocaleString()} {t('leaderboard.points')}
            </span>
          </div>
          <div className="relative h-2 bg-gray-700 rounded-full overflow-hidden">
            <motion.div
              className="absolute top-0 left-0 h-full bg-gradient-to-r from-purple-500 to-pink-500"
              initial={{ width: 0 }}
              animate={{ width: `${progressPercent}%` }}
              transition={{ duration: 1, ease: 'easeOut' }}
            />
          </div>
          <div className="text-xs text-gray-400">
            {t('pointsNeeded', { points: status.pointsToNextMilestone })}
          </div>
        </div>
      )}

      {/* Tier and Leaderboard */}
      <div className="flex items-center gap-4 pt-4 border-t border-gray-700">
        {/* Current Tier */}
        <div className="flex items-center gap-2">
          <span className="text-sm text-gray-400">Tier:</span>
          <span
            className={`px-3 py-1 rounded-full text-sm font-medium capitalize ${
              tierColors[status.tier]
            }`}
          >
            {status.tier}
          </span>
        </div>

        {/* Leaderboard Position */}
        {status.leaderboardPosition !== null && (
          <div className="flex items-center gap-2 ml-auto">
            <TrendingUp className="w-4 h-4 text-green-500" />
            <span className="text-sm text-gray-400">{t('position')}:</span>
            <span className="text-sm font-semibold text-white">
              #{status.leaderboardPosition}
            </span>
          </div>
        )}
      </div>
    </div>
  );
}
