'use client'

import { useTranslations } from 'next-intl'
import { Achievement } from '@/types'
import * as Icons from 'lucide-react'

interface AchievementCardProps {
  achievement: Achievement
  unlocked: boolean
}

export function AchievementCard({ achievement, unlocked }: AchievementCardProps) {
  const t = useTranslations('rewards')
  const Icon = (Icons as any)[achievement.iconName] || Icons.Trophy

  return (
    <div className="relative overflow-hidden">
      <div
        className={`
          relative rounded-lg border p-6 transition-all duration-200
          ${unlocked
            ? 'border-gray-200 bg-white hover:scale-105 hover:shadow-md dark:border-gray-700 dark:bg-gray-800'
            : 'border-gray-200 bg-gray-50 dark:border-gray-700 dark:bg-gray-800/50'
          }
        `}
      >
        {/* Category Badge */}
        <div className="absolute right-4 top-4">
          <span className="inline-flex items-center rounded-full bg-blue-50 px-2 py-1 text-xs font-medium text-blue-700 dark:bg-blue-900/30 dark:text-blue-300">
            {t(`achievements.categories.${achievement.category}`)}
          </span>
        </div>

        {/* Icon */}
        <div className={`mb-4 ${unlocked ? '' : 'opacity-50'}`}>
          <div className="inline-flex h-12 w-12 items-center justify-center rounded-lg bg-gradient-to-br from-blue-500 to-purple-600">
            <Icon className="h-6 w-6 text-white" />
          </div>
        </div>

        {/* Title & Description */}
        <h3 className={`mb-2 text-lg font-semibold ${unlocked ? 'text-white' : 'text-gray-500 dark:text-gray-400'}`}>
          {achievement.name}
        </h3>
        <p className={`mb-4 text-sm ${unlocked ? 'text-gray-600 dark:text-gray-300' : 'text-gray-400 dark:text-gray-500'}`}>
          {achievement.description}
        </p>

        {/* Points Badge */}
        <div className="flex items-center justify-between">
          <div className={`inline-flex items-center rounded-full px-3 py-1 text-sm font-medium ${
            unlocked
              ? 'bg-green-50 text-green-700 dark:bg-green-900/30 dark:text-green-300'
              : 'bg-gray-100 text-gray-500 dark:bg-gray-700 dark:text-gray-400'
          }`}>
            <Icons.Award className="mr-1 h-4 w-4" />
            {achievement.points} {t('leaderboard.points')}
          </div>
        </div>

        {/* Lock Overlay */}
        {!unlocked && (
          <div className="absolute inset-0 flex items-center justify-center rounded-lg bg-gray-900/10 backdrop-blur-[2px] dark:bg-gray-900/20">
            <Icons.Lock className="h-8 w-8 text-gray-400 dark:text-gray-500" />
          </div>
        )}
      </div>
    </div>
  )
}
